body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#background {
  /* So that it appears under the root */
  /* https://stackoverflow.com/questions/5218927/z-index-not-working-with-fixed-positioning */
  z-index: -1;
  position: fixed;
}

#hello {
  display: inline-block;
  color: white;
  font-size: 150px;
  font-weight: 200;
  text-align: center;
  transform: translate(calc(50vw - 50%), calc(50vh - 70%));
  /* animation: movearound 2s infinite; */
  animation-direction: alternate;
}

/* Animation Keyframes*/
@keyframes movearound {
  from {
    transform: scale(0.3) translate(70vw, 70vh);
  }
  to {
    transform: scale(1) translate(30vw, 30vh);
  }
}
